.App {
  text-align: center;
  background-color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.header {
  background-color: #282c34;
  background-image: url("./header.jpeg");
  background-size:inherit;
  height:500px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(37, 166, 175);
}


.App-link {
  color: #61dafb;

}
.productbutton{
  border:none;
  width:55px;
  background-color:white;
  margin:none;
  padding:none;
  font-size: 15px;
  text-align:left;
  align-content: center;


}
img{
  margin-top: 100px;
  width:300px;
  height:300px;
}
.App >div{
  display:flex;
  flex-direction: row;
  margin: auto;
  margin-top:0%;
  padding:0px;
  align-self:left;
  text-align: left;
  align-items: left;
}
.buyButton{
 
  background-color:rgb(137, 32, 178);
  width:100px;
  height:40px;
  padding:1px;
  align-content: center;
  align-self: left;
  align-items: center;

}
.Products{
  display:flex;
  flex-direction:column;
  margin: auto;
  align-self: left;
  align-items: left;


}
.container {
  display: grid;
  grid-template-columns: 3fr 1fr;
  text-align: left;
  align-self: left;

}
.shoppingcart {
  margin: 25px;
  width: 200px;
  height:auto;
  list-style-type: none;
  background-color:#2b2d68;
  color: #fff;
  align-content: center;
  text-align: center;
  align-items: center;
}
.Shoppingcart{
  margin:45px;
  background-color:#2b2d68;
  align-content: center;
  text-align: center;
  align-items: center;
}
.productButtonMore {
  background-color: rgb(137, 32, 178);
}
.productlist{
  margin:10px;
  width: 100px;
  height: auto;
  text-align: left;
 
}
.emptyProductButton {
  background-color: rgb(137, 32, 178);
}
.emptyAllButton{
  background-color: rgb(137, 32, 178);
  align-content: center;
 
}

.productbutton_kuvaus{
  align-content: left;
  text-align: left;
  font: inherit;
  margin: 0px;
  height:10px;
  width:15px;
  background-color: white;
  border-color: white;
  border-bottom: none;
  border-right: white;
}

